<template>
  <div class="image">
    <div class="divCen">
      <h3>{{ msg }}</h3>
      <h4>学生管理面板</h4>
      <p class="p-right">
        <router-link :to="'/info/'+mode" active-class="active">返回&nbsp;&nbsp;&nbsp;&nbsp;</router-link>
      </p>
      <table>
      <tr>
        <th>Id</th>
        <td>
          <input type="text" v-model="id" />
        </td>
      </tr>
      <tr>
        <th>姓名</th>
        <td>
        <input type="text" v-model="name" />
        </td>
      </tr>
      <tr>
        <th>性别</th>
        <td>
        <input type="radio" value="男" v-model="gender" />男
        <input type="radio" value="女" v-model="gender" />女
        </td>
      </tr>
      <tr>
        <th>年龄</th>
        <td>
        <input type="text" v-model="age" />
       </td>
      </tr>
      <tr>
        <th>班级</th>
        <td>
        <input type="text" v-model="grade" />
        </td>
      </tr>
      <tr>
        <th>语文</th>
        <td>
        <input type="text" v-model="chinese" />
        </td>
      </tr>
      <tr>
        <th>数学</th>
        <td>
        <input type="text" v-model="math" />
        </td>
      </tr>
      <tr>
        <th>英语</th>
        <td>
        <input type="text" v-model="english" />
        </td>
      </tr>
    </table>
      <p>
        <button @click="btn_edit_stuinfo">保存</button>
      </p>
    </div>
  </div>
  </template>
  
  <script src="https://cdn.jsdelivr.net/npm/axios/dist/axios.min.js"></script>
  <script>
  import axios from 'axios'
  export default {
    name: "StudentEdit",
    data() {
      return {
        mode: "",
        msg: "欢迎来到学生管理系统",
        id: "",
        name: "",
        gender: "",
        age:"",
        grade:"",
        chinese: "",
        math: "",
        english: "",
        mean:"",
        score:"",
        url:`http://localhost:8081/edit_user/`
      };
    },
    created: function () {
      this.mode = this.$route.params.mode;
      this.url=this.url+this.mode;
      this.id = this.$route.params.id;
      let stuinfo = JSON.parse(localStorage.getItem("stu" + this.id));
      this.name = stuinfo.name;
      this.gender = stuinfo.gender;
      this.age = stuinfo.age;
      this.grade = stuinfo.grade;
      this.chinese = stuinfo.chinese;
      this.math = stuinfo.math;
      this.english = stuinfo.english;
    },
    methods: {
      btn_edit_stuinfo: function () {

      if(this.name==null||this.name==""||this.gender==null||this.gender==""||this.chinese==null||this.chinese==""||this.math==null||this.math==""||this.english==null||this.english==""){
        window.alert("输入框不能为空");
        return false;
      }else if(!isNaN(this.name)){
        window.alert("请正确输入姓名");
        return false;
      }else if(this.age>50||this.age<12){
        window.alert("请正确输入年龄")
        return false;
      }else if(!/^20[0-3][0-9]年([0-9]{1,2})班$/.test(this.grade)){
        window.alert("请正确输入班级(2021年15班)")
        return false;
      }else if(isNaN(this.chinese)||isNaN(this.math)||isNaN(this.english)){
        window.alert("学生的成绩只能为数字");
        return false;
      }else if(!(this.chinese>=0&&this.chinese<=100)||!(this.english>=0&&this.english<=100)||!(this.math>=0&&this.math<=100)){
        window.alert("学生的成绩只能在0~100内");
        return false;
      }
        let oStu = {
          id: this.id,
          name: this.name,
          gender: this.gender,
          age: this.age,
          grade: this.grade,
          chinese: this.chinese,
          math: this.math,
          english: this.english,
          mean: parseFloat((this.chinese*1+this.math*1+this.english*1)/3).toFixed(1),
          score: this.chinese*1+this.math*1+this.english*1,
        };
        var key = "stu" + oStu.id;
        var stu = JSON.stringify(oStu);
        localStorage.setItem(key, stu);

        if(oStu.gender=="男"){
          oStu.gender=1;
        }else{
          oStu.gender=0;
        }
        const regex=/\d+/g;
        const numbers=oStu.grade.match(regex);
        if(numbers[0].length==1){
          numbers[0]=0+numbers[0];
        }
        if(numbers[1].length==1){
          numbers[1]=0+numbers[1];
        }
        oStu.grade=(numbers[0].substring(2,4))+numbers[1];
        
        var stu=JSON.stringify(oStu);

        axios.post(this.url,stu).then(res=>{
          console.log("学生信息已修改")
        })

        let next="/info/"+this.mode;
        console.log(next);
        this.$router.replace({ path: next});
      },
    },
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  h3,
  h4 {
    font-weight: normal;
    color:#fdfdfd;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  input {
    width: 128px;
  }
  p {
    font-style: normal;
  }
  p.p-right {
    font-style: italic;
    text-align: right;
  }
  div.image{
    background: url("th.jpg");
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed
  }
  div.divCen {
    margin: 2px auto;
    width: auto;
  }
  table {
    width: 60%;
    margin: auto;
    border-collapse: collapse;
    border: 0;
    text-align: center;
  }
  th {
    background-color: #93daff;
    color: #000000;
  }
  th,
  td {
    font-size: 0.95em;
    text-align: center;
    padding: 4px;
    border-collapse: collapse;
  }
  th,
  td {
    border: 1px solid #c1e9fe;
    border-width: 1px 0 1px 0;
  }
  tr {
    border: 1px solid #c1e9fe;
  }
  tr:nth-child(odd) {
    background-color: #dbf2fe;
  }
  tr:nth-child(even) {
    background-color: #fdfdfd;
  }
  </style>
  