<template>
    <div class="image">
      <h3>{{ msg }}</h3>
      <h4>学生管理面板</h4>
      <p class="p-right">
        <router-link to="/" active-class="active">返回&nbsp;&nbsp;&nbsp;&nbsp;</router-link>
      </p>
      <table v-if="loaded">
        <tr>
          <th>序号</th>
          <th>Id</th>
          <th>姓名</th>
          <th>性别</th>
          <th>年龄</th>
          <th>班级</th>
          <th>语文</th>
          <th>数学</th>
          <th>英语</th>
          <th>平均分</th>
          <th>总成绩</th>
          <th>管理</th>
        </tr>
        <tr v-for="(s, index) in stu" :key="index">
          <td>{{ index+1 }}</td>
          <td>{{ s.id }}</td>
          <td>{{ s.name }}</td>
          <td>{{ s.gender }}</td>
          <td>{{ s.age }}</td>
          <td>{{ s.grade }}</td>
          <td>{{ s.chinese }}</td>
          <td>{{ s.math }}</td>
          <td>{{ s.english }}</td>
          <td>{{ s.mean }}</td>
          <td>{{ s.score }}</td>
          <td>
            <router-link :to="'/insert/'+ mode " active-class="active">添加&nbsp;&nbsp;&nbsp;</router-link>
            <router-link :to="'/edit/'+ mode +'/'+ s.id" active-class="active">编辑&nbsp;&nbsp;&nbsp;</router-link>
            <a :href="'http://localhost:8082/#/info/'+mode" @click="del_stu_info(s.id)" active-class="active">删除</a>
          </td>
        </tr>
      </table>
    </div>
  </template>
  
  <script src="https://cdn.jsdelivr.net/npm/axios/dist/axios.min.js"></script>
  <script>
  import axios from 'axios'
  export default {
    name: "StudentInfo",
    data() {
      return {
        mode:"",
        msg: "欢迎来到学生管理系统",
        students:Array(),
        len:3,
        meanAll:Array(),
        stuId:Array(),
        loaded:false,
        url:`http://localhost:8081`,
      };
    },
    created:function(){
      this.mode=this.$route.params.id;
      axios.get(`${this.url}/get/${this.mode}`).then(res=>{
        console.log(res.data)
        this.len=res.data.length
        for(let i=0;i<this.len;i++){
          this.students[i]=res.data[i]
        }
        for(let i=0;i<this.len;i++){
          if(this.students[i].gender.data==1){
            this.students[i].gender="男";
          }else{
            this.students[i].gender="女";
          }
          let grade=parseInt((this.students[i].grade)/100);
          let clas=(this.students[i].grade)%100;
          if(grade<10&&grade>0){
            this.students[i].grade=`200${grade}年${clas}班`;
          }else{
            this.students[i].grade=`20${grade}年${clas}班`;
          }
        }
        for (let i = 0; i <this.len; i++) {
          this.meanAll.push(this.students[i].mean);
        }
        console.log(this.students); 
        this.loaded=true;
         })
    },
    computed: {
      stu: function () {
        let max;
        let stu=Array();
        let order =Array();
        let num =Array();
        for (let i = 0; i < this.len; i++) {
          num.push(i);
        }
        for (let j = 0; j < this.len; j++) {
          let alter;
          for (let k = j; k < this.len; k++) {
            if((this.meanAll[j]*1.0)<(this.meanAll[k]*1.0)){
                alter=this.meanAll[k];
                this.meanAll[k]=this.meanAll[j];
                this.meanAll[j]=alter;
                alter=num[k];
                num[k]=num[j];
                num[j]=alter;
              }
          }
          max=num[j];
          order.push(max);
        }
        for (let i = 0; i < this.len; i++) {
            stu.push(this.students[num[i]]);
            localStorage.setItem("stu"+this.students[num[i]].id,JSON.stringify(this.students[num[i]]));
        }
        return stu;
      },
    },
    methods:{
      del_stu_info: function (stuid) {
        const url = `${this.url}/del/${this.mode}/${stuid}`;
        axios.delete(url).then(res=>{
        console.log("进行删除操作");
        })
        localStorage.removeItem("stu" + stuid);
        location.reload();
      }
    }
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  h3,
  h4 {
    font-weight: normal;
    color:#fdfdfd;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  div.image{
    background: url("th.jpg");
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed
  }
  table {
    width: 98%;
    margin: 16px 0;
    border-collapse: collapse;
    border: 0;
  }
  th {
    background-color: #93daff;
    color: #000000;
  }
  th,
  td {
    font-size: 0.95em;
    text-align: center;
    padding: 4px;
    border-collapse: collapse;
  }
  th,
  td {
    border: 1px solid #c1e9fe;
    border-width: 1px 0 1px 0;
  }
  tr {
    border: 1px solid #c1e9fe;
  }
  tr:nth-child(odd) {
    background-color: #dbf2fe;
  }
  tr:nth-child(even) {
    background-color: #fdfdfd;
  }
  p.p-right {
    font-style: italic;
    text-align: right;
  }
  </style>
  